.product {
  &s {
    display: flex;
    flex-direction: column;
    gap: 30px;
    &__title {
      font-size: 30px;
      font-weight: 500;
      color: var(--gray);
    }
    &__list {
      display: flex;
      flex-wrap: wrap;
      gap: 40px;
    }
  }
  &__gallery {
    display: flex;
    flex-direction: column;
    gap: 30px;
    &__main {
      width: 100%;
      position: relative;
      padding: 90px 60px;
      &__panel {
        position: relative;
        padding: 60px;
        border-radius: 60px;
        background-color: var(--white);
        width: 100%;
        max-width: 565px;
        min-height: 540px;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &__body {
          display: flex;
          flex-direction: column;
          gap: 30px;
        }
        &__title {
          font-size: 40px;
          color: var(--gray);
        }
        &__text {
          color: var(--gray);
        }
        &__footer {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .swiper-button-prev,
          .swiper-button-next {
            color: var(--gray);
            border: 1px solid var(--gray);
            &:hover {
              border-color: var(--theme);
            }
          }
          .progressbar {
            &__line {
              display: none;
            }
            &__counts {
              color: var(--gray-lightest);
              span:first-child {
                color: var(--gray);
              }
            }
          }
        }
      }
      .swiper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }
      .swiper-slide {
        img {
          display: none;
        }
        a {
          border-radius: 50px;
          overflow: hidden;
          display: block;
          width: 100%;
          height: 100%;
          background-size: cover;
          background-position: center center;
          background-repeat: no-repeat;
        }
      }
    }
    &__thumbs {
      overflow: visible;
      width: 100%;
      .swiper-slide {
        cursor: pointer;
        img {
          width: 100%;
          height: auto;
          opacity: 0.5;
          border-radius: 25px;
          transition: all 0.3s;
          overflow: hidden;
        }
        &:hover,
        &-thumb-active {
          img {
            opacity: 1;
          }
        }
      }
    }
  }
  .content-block--text,
  .content-block--button {
    max-width: 100%;
  }
  .tabs {
    &__contents {
      overflow: visible;
    }
    &__buttons {
      margin-bottom: 0;
      margin-top: 60px;
    }
  }
  &--card {
    border-radius: 50px;
    background-color: var(--white);
    overflow: hidden;
    padding: 60px 30px 30px;
    flex-shrink: 0;
    width: calc(33.33333% - 40px + 40px / 3);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 45px;
    .product {
      &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
      }
      &__image {
        img {
          max-width: 100%;
          width: auto;
          height: auto;
        }
      }
      &__title {
        font-size: 20px;
        color: var(--gray);
      }
      &__gabarite {
        color: var(--gray-light);
        font-size: 15px;
      }
    }
  }
}
